import React, { useState, useEffect, FormEvent, FC } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './ResetPasswordPage.module.css';
import { sendPassword } from '../../utils/api';

export const ResetPasswordPage: FC = () => {
    const [name, setName] = useState('');
    const [password, setPassword] = useState('');
    const navigate = useNavigate();
    const [error, setError] = useState('');

    useEffect(() => {
        const passwordResetDone = localStorage.getItem('passwordResetDone');

        if (!passwordResetDone) {
            navigate('/forgot-password');
        }

        localStorage.removeItem('passwordResetDone');
    }, [navigate]);

    const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        sendPassword(password, name)
            .then(res => {
                navigate('/login', { replace: true });
            })
            .catch(error => {
                setError(',  !');
            });
    };

    return (
        <div className={styles.wrapper}>
            <div className={styles.container}>
                <form onSubmit={handleSubmit} className={styles.content}>
                    <h1 className='text text_type_main-medium mb-6 mt-10'> </h1>

                    <Input
                        type={'password'}
                        placeholder={'  '}
                        onChange={e => setPassword(e.target.value)}
                        icon={'ShowIcon'}
                        name={'password'}
                        value={password}
                        error={false}
                        errorText={''}
                        size={'default'}
                        extraClass='mb-6'
                    />

                    <Input
                        type={'text'}
                        placeholder={'   '}
                        onChange={e => setName(e.target.value)}
                        name={'name'}
                        value={name}
                        error={false}
                        errorText={''}
                        size={'default'}
                        extraClass='mb-6'
                    />

                    {error && (
                        <p className={`${styles.errorMessage} text text_type_main-default mb-4`} >
                            {error}
                        </p>
                    )}

                    <Button htmlType="submit" type="primary" size="medium" extraClass='mb-20'>
                        
                    </Button>

                    <div className='text text_type_main-small mb-4'>
                        <span className="text_color_inactive"> ?</span>
                        <Link to='/login' className={styles.link}> </Link>
                    </div>

                </form>
            </div>
        </div>

    );
}