import { useEffect, FC } from 'react';
import CheckMarkIconImage from '../../images/CheckMarkIcon.png';
import styles from './OrderDetails.module.css';
import { useAppDispatch } from '../../services/hooks/hooks';
import { fetchOrderNumberRequest, updateOrderNumber } from '../../services/actions/orderDetails';

import { OrderDetailsProps } from '../../services/types/types';

const OrderDetails: FC<OrderDetailsProps> = ({ orderId }) => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(fetchOrderNumberRequest());
    dispatch(updateOrderNumber(orderId));
  }, [dispatch, orderId]);

  return (
    <>
      <h2 className={`${styles.textShadows} text text_type_digits-large mb-8`} data-cy="orderNumber">
        {orderId}
      </h2>
      <p className='text text_type_main-medium'> </p>
      <div className='mb-15 mt-15'>
        <img src={CheckMarkIconImage} alt="  " />
      </div>
      <p className='text text_type_main-default mb-2'>   </p>
      <p className='text text_type_main-default text_color_inactive'>
            
      </p>
    </>
  );
}

export default OrderDetails;